export default{
	data() {
		return {
			accounts:'',
			pwd:'',
			show:0
		}
	},
	methods:{
		jump(url){
			uni.navigateTo({
				url:url
			})
		},
		login(){
			let that = this
			if(!that.accounts){
				uni.showToast({
					icon: 'none',
					title: '请输入用户名或者手机号'
				});
				return;
			}
			if(!that.pwd){
				uni.showToast({
					icon: 'none',
					title: '请输入密码'
				});
				return;
			}
			uni.showLoading({
				title: '登录中',
				mask: true
			});
			let times = setTimeout(function() {
				uni.hideLoading()
				uni.showToast({
					icon: 'none',
					title: '网络请求错误，请稍后再试'
				})
				return
			}, 10000)
			uni.request({
				url:that.APIconfig.api_root.login.login,
				method:'POST',
				data:{
					username:that.accounts,
					password:that.pwd,
					time:Date.parse(new Date())/1000
				},
				success(res) {
					uni.hideLoading()
					clearTimeout(times)
					console.log(res)
					let data = res.data 
					uni.showToast({
						icon:'none',
						title:data.msg
					})
					if(data.code == 0){
						console.log(data.data.memberInfo)
						// uni.setStorageSync('user',JSON.stringify())
						that.$store.commit('change_login')
						that.$store.commit('state_user',data.data.memberInfo)
						that.$store.commit('state_token',data.token)
						console.log(that.$store.state)










						
						setTimeout(function(){
							
							uni.switchTab({
								url: '../index/index'
							});
						},1000)
					}
				}
			})
		},
		wx(){
			let that = this
			
			
			




			
			











































		}
	},
	onShow() {











	}
}
